<template>
  <div class="messageCenterBox">
    <div v-if="List.length > 0">
      <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
        <template #pulling><refreshLoading text="下拉刷新"/></template>
        <template #loosing><refreshLoading text="下拉刷新"/></template>
        <template #loading><refreshLoading text="加载中"/></template>
        <template #success><refreshLoading text="加载完成"/></template>
        <van-list :immediate-check="false" :finished="finished" v-model="loading" finished-text="没有更多啦~" @load="onLoad">
          <div class="news" @click="toHelpDetail(i.id, i.relationId, k)" v-for="(i, k) in List" :key="k">
            <div class="news_top">
              <div class="news_top_l">{{ i.title }}</div>
              <div class="news_top_r">
                <div class="data">{{ i.date }}</div>
                <div class="circular" v-if="i.isRead === 0"></div>
              </div>
            </div>
            <div class="line"></div>
            <div class="news_bottom">{{ i.content }}</div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
    <div class="messageBox" v-else>
      <img class="messageClass" src="./../../assets/imgs/message/noMessage.png" alt="">
      <div>暂无平台消息</div>
    </div>
  </div>
</template>
<script>
import refreshLoading from '@/components/refreshLoading'
import { messageCenter } from '@/http/service/message-center'
import Vue from 'vue';
import { Toast } from 'vant';
Vue.use(Toast);
export default {
  components: {
    refreshLoading
  },
  data() {
    return {
      List: [],
      loading: false,
      finished: false,
      refreshing: false,
      pageNo: 1
    }
  },
  methods: {
    /**
     * @description 获取数据
     */
    getList() {
      messageCenter({ messageType: 3, pageNo: this.pageNo, pageCount: 10 }).then(res => {
        if (res.data.success) {
          this.List = res.data.data
        } else {
          Toast.fail({ message: res.data.message, duration: 2000 })
        }
      })
    },
    /**
     * @description 跳转到消息详情页
     * @param {id，relationId, k}  id, 消息id ，relationId：relationId， k 当前索引，点击点前消息变成已读，跳转详情页
     */
    toHelpDetail(id, relationId, k) {
      this.List[k].isRead = 1
      this.$router.push(`/messageDetail/${id}/${relationId}`);
      // this.$router.push({
      //   path: '/messageDetail',
      //   query: {
      //     id: id,
      //     relationId: relationId,
      //   },
      // })
    },
    /**
     * @description 上拉加载
     */
    onLoad() {
      this.pageNo++
      messageCenter({ messageType: 3, pageNo: this.pageNo, pageCount: 10 }).then(res => {
        let platList = res.data.data
        if (res.data.success) {
          if (res.data.data.length < 10) {
            this.finished = true
          }
          this.loading = false
          this.List = this.List.concat(platList)
        }
      })
    },
    /**
     * @description 下拉刷新
     */
    onRefresh() {
      setTimeout(() => {
        this.pageNo = 1
        this.getList()
        this.refreshing = false
      }, 1000)
    }
  },
  /**
   * @description 请求数据没有登录跳转到登录页面
   */
  created() {
    if (this.$util.isHaierApp()) {
      this.$store.dispatch('handleUpAuth').then(isLogin => {
        // 登陆正常 调用加购接口
        if (isLogin) {
          this.getList();
        } else {
          this.$store.dispatch('doLogin')
        }
      })
    } else {
      this.getList();
    }
  },
  /**
   * @description 利用路由守卫判断路由去向，进入详情页不刷新
   */
  beforeRouteLeave(to, from, next) {
    const context = this
    if (to.path === '/messageDetail') {
      context.$route.meta.keepAlive = true;
    } else {
      context.$route.meta.keepAlive = false;
    }
    next()
  }
}
</script>
